html, body {
  height: 100%; }

.topbanner {
  position: relative;
  width: 1200px;
  height: 530px;
  text-align: center;
  overflow: hidden; }
  .topbanner .imglist li, .topbanner .imglist li img, .topbanner .community .inner .item img, .community .inner .item .topbanner img, .topbanner .community .inner .item .cover, .community .inner .item .topbanner .cover {
    width: 1200px;
    height: 530px; }
  .topbanner .imglist {
    position: absolute;
    left: 0;
    height: 530px; }
    .topbanner .imglist li {
      float: left; }
  .topbanner .dotlist {
    display: inline-block;
    position: relative;
    top: 450px; }
    .topbanner .dotlist li {
      float: left;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: url("../../../images/sprite_dot.png") 0 -12px no-repeat;
      margin-left: 10px;
      margin-right: 10px; }
      .topbanner .dotlist li.active {
        background: url("../../../images/sprite_dot.png") 0 0 no-repeat; }

.community {
  position: relative; }

.community .inner {
  margin-left: -26px;
  width: 1226px; }
  .community .inner li {
    display: none; }
    .community .inner li.active {
      display: block; }
  .community .inner .item {
    float: left;
    margin-left: 26px;
    margin-bottom: 30px; }
    .community .inner .item .topbanner .imglist li, .topbanner .imglist .community .inner .item li, .community .inner .item img, .community .inner .item .cover {
      width: 280px;
      height: 415px; }
    .community .inner .item p {
      position: absolute;
      height: 50px;
      width: 280px;
      margin-top: -50px;
      font: 16px;
      color: #fff;
      text-align: center;
      line-height: 50px;
      background: url("../../../images/space.png") 0 0 repeat;
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
      background: rgba(0, 0, 0, 0.6); }
    .community .inner .item .cover {
      position: absolute;
      margin-top: -415px;
      background: transparent;
      opacity: 0;
      filter: alpha(opacity=0);
      background: url("../../../images/space.png") 0 0 repeat; }
      .community .inner .item .cover:hover {
        background: rgba(0, 0, 0, 0.3);
        opacity: 1;
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }
    .community .inner .item .cover {
      text-align: center;
      color: #fff; }
      .community .inner .item .cover .info {
