        font-size: 12px; }
      .community .inner .item .cover a {
        position: relative;
        display: inline-block;
        margin: 0 auto;
        width: 120px;
        height: 40px;
        color: #fff;
        font-size: 16px;
        text-align: center;
        line-height: 40px;
        border: 1px solid #fff; }

.creatRice {
  float: right;
  width: 150px;
  height: 44px;
  margin-top: 8px;
  color: #fff;
  font-size: 14px;
  border: 0;
  background-color: #000;
